@import'../common/common.scss';


.header{
    background-color:$secondary-color;
    height: 72px;
    padding: 0px 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.logo{
    color: $font-primary;
    font-size: 30px;
    font-weight: 600;
}

.image, .image img{
    width: 38px;
    height: 38;

}

.search-bar{
    width: 30%;
    display: flex;
    justify-content: center;

    form{
        display: flex;
        justify-content: center;
        width: 70%;

        button{
            padding: 0px 8px;
            font-size: 20px;
            cursor: pointer;
            height: 38px;


        }
    }

    input{
        font-size: 18px;
        width: 100%;
        padding: 5px 5px 5px 10px;
        height: 38px;
        outline: none;
    }
}
